import axios from 'axios'

var allf={
  delay:function(x) {  //延遲
    return new Promise(function (resolve) {
      setTimeout(() => {
	    resolve();
      }, x);
    });	  
  },
  get_data:async function() {  
	return await axios.get("http://www.e9life.com/api/AddrTurnName/Get?page=1")
	.then(res=>{
      return res.data.response.data
	})
  },
  get_case_user:async function(x) {  //取得個案-分頁
	return await axios.get("https://api.donkeymove.com/api/CaseUser/Get?page="+x+"&key=&isDescending=true&pMode=1")
	.then(res=>{
      return res.data.response
	})
  },
  reload:function() {
	alert(123)
  }
}
export default allf


